.header {
    display: flex;
    height: 100%;
    align-items: center;
    flex-direction: column;
}
*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Mulish', sans-serif;
}

.navbar {
  background-color: #fff !important;
}

.navbar-brand {
  font-size: 1.9rem !important;
  color: #565387 !important;
}

.navbar a {
  font-size: 1.3rem;
  text-transform: capitalize;
}

.menu_active {
  font-weight: bold;
  /* border-bottom: 1px solid #565387; */
  border-bottom: none;
  color: #3498db !important;
}

.navbar a:hover {
  color: #3498db !important;
}

@media (max-width: 991px) {
    .navbar-brand {
        font-size: 1.5rem !important;
    }
    .menu_active {
        font-weight: bold;
        border-bottom: none;
    }
}

.root {
    height: 100vh;
    display: flex;
    width: auto;
    flex-direction: column;
}

footer {
    text-align: center;
    padding: 6px;
}
